    import mylatestudent from "../../../components/mylate_student.vue";
	export default {
		data() {
			return {
				username:'',
				actlist: []
			}
		},
		components:{
			mylatestudent,
		},
		methods: {
			options: function() {
				// this.$store.state.projectName=this.list.gcmc;
				uni.navigateTo({
					url: '../../my_project/late_record/late_record'
				})
			}
		},
		onLoad: function(option){
			this.class=option.class;
			// this.username=option.username;
			// console.log(option.username);
			uni.setNavigationBarTitle({
				title: this.class
			});
			uni.request({
				url: this.api + '/basicdata/getmystudent.php',
				data: {
					flag:'latestudent',
					class:option.class,
				},
				method: 'POST',
				dataType: 'json',
				header: {
					'content-type': 'application/x-www-form-urlencoded', //POST头信息
				},
				success: (res) => {
					// console.log(res.data.username);
					console.log(res);
					res=res.data;
					// this.username=res.username
					// this.actlist=res.data.username;
					if(res[res.length-1].result=='success'){
						res.splice(res.length-1,1);
						this.actlist=res;
					}else{
						uni.showToast({
							icon: 'error',
							position: 'bottom',
							title: '无数据'
						});
					}
				},
				fail: (error) => {
					console.log(error);
				}
			});
		}
		
		

	}
